

/* horizen
-------------------------------------------------- */


.p-card-h {
  border: 1px solid #ddd;
  background: #fff;
  margin-top: -1px;

  @include m(img) {
    img {
      width: 100%;
      height: auto;
    }
  }
  @include m(text) {
    margin-bottom: 7px;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.6;
    padding: 0 6.11354%;
  }
  @include m(title) {
    margin-bottom: 7px;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.6;
  }
  @include m(date) {
    color: #999;
    font-size: 12px;
  }
  @include e(cat) {
    font-size: 12px;
    a {
      color: #999;
      &:hover {
        color: #050505;
      }
      &:visited {
        color: #999;
      }
    }
  }

}
@media screen and (min-width: 991px) {
  .p-card-h {
    @include m(inner) {
      display: grid;
      grid-template-columns: 1fr 2fr;
      align-items: center;
    }
    @include m(img) {
    }
  }
}

