-------------------------------------------------- */
/* breakpoint
-------------------------------------------------- */
.l-wrapper {
  display: grid;
  min-height: 100vh;
  width: 100%;
  grid-template-rows: 100px auto auto;
}

.l-container {
  width: 92.1875%;
  max-width: 1180px;
  margin: 0 auto;
}

.l-container-sub {
  width: 92.1875%;
  max-width: 1180px;
  margin: 0 auto;
}

.l-sec, .l-aside {
  padding: 60px 0;
}

.l-sec-b {
  padding-bottom: 60px;
}

.l-sec-b__header {
  margin-bottom: 30px;
}

.l-sec-gray {
  background: #f5f5f5;
}

.l-footer {
  grid-row: 3;
  background-color: #2f9839;
  color: #fff;
  margin-top: 150px;
}

.l-footer .l-footer-wrap {
  display: grid;
  grid-template-rows: auto auto;
  grid-template-columns: 50% 50%;
}

.l-footer--left {
  grid-row: 1 / 2;
  grid-column: 1 / 3;
  margin-bottom: 50px;
  border-bottom: 1px solid #ddd;
  padding: 0 50px 0 0;
}

.l-footer--left h3 {
  margin-bottom: 30px;
}

.l-footer--left p {
  line-height: 2;
}

.l-footer--center {
  grid-row: 2 / 2;
  grid-column: 1 / 2;
}

.l-footer--right {
  grid-row: 2 / 2;
  grid-column: 2 / 2;
}

.l-footer .l-footer-menu li {
  font-size: 14px;
  line-height: 1.5;
  margin: 0 0 20px 0;
}

.l-footer .l-footer-menu li a {
  color: #fff;
}

.l-footer .l-footer-menu li a::hover {
  color: #fff;
}

.l-footer .l-footer-bottom {
  background: #fff;
}

.l-footer .l-footer-copyright {
  color: #000;
  text-align: center;
}

@media (min-width: 1200px) {
  .l-footer .l-footer-wrap {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: auto;
  }
  .l-footer--left {
    grid-column: 1;
    grid-row: 1;
    border-right: 1px solid #ddd;
    border-bottom: none;
    margin-bottom: 0;
    padding: 0 50px 0 0;
  }
  .l-footer--center {
    grid-column: 2;
    grid-row: 1;
    border-right: 1px solid #ddd;
    padding: 0 48px;
  }
  .l-footer--right {
    grid-column: 3;
    grid-row: 1;
    padding: 0 48px;
  }
}

.l-header {
  grid-row: 1;
  display: block;
  width: 100%;
}

.l-global-nav {
  display: grid;
  min-height: 100px;
  grid-auto-flow: column;
  grid-gap: 1.5em;
}

.l-main {
  grid-row: 2;
  overflow: auto;
}

.l-main-sub {
  overflow: auto;
}

.l-main-sub .l-content-wrap {
  display: grid;
  grid-template-columns: 1fr;
  width: 92.1875%;
  max-width: 1180px;
  margin: 0 auto;
}

.l-main-sub .l-content-wrap .l-content {
  line-height: 2;
}

@media (min-width: 1200px) {
  .l-main-sub .l-content-wrap {
    display: grid;
    grid-template-columns: 3fr 1fr;
    grid-gap: 20px;
  }
}

.hero-sub {
  width: 100%;
  min-width: inherit;
  height: 350px;
}

@media screen and (min-width: 1260px) {
  .hero-sub {
    min-width: 1180px;
    height: 450px;
  }
}

.profile .hero-sub {
  background: url(dist/img/sub-vidual01.jpg) no-repeat center center;
  background-size: cover;
  filter: sepia(70%);
}

.idea .hero-sub {
  background: url(dist/img/sub-vidual02.jpg) no-repeat center center;
  background-size: cover;
}

.policy .hero-sub {
  background: url(dist/img/sub-vidual03.jpg) no-repeat center center;
  background-size: cover;
}

.activity_report .hero-sub {
  background: url(dist/img/sub-vidual04.jpg) no-repeat center center;
  background-size: cover;
}

.activity .hero-sub {
  background: url(dist/img/sub-vidual05.jpg) no-repeat center center;
  background-size: cover;
}

.blog .hero-sub, .post .hero-sub {
  background: url(dist/img/sub-vidual06.jpg) no-repeat center center;
  background-size: cover;
}

.report .hero-sub {
  background: url(dist/img/sub-report.png) no-repeat center center;
  background-size: cover;
}

.c-menu-btn {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  text-align: center;
}

.c-box {
  padding: 17px;
  padding-bottom: 55px;
}

.c-box:first-of-type {
  border-right: none;
}

.p-cards {
  display: grid;
  grid-template-columns: 1fr;
  align-items: stretch;
}

.p-cards .p-card {
  position: relative;
  margin-bottom: 50px;
}

.p-cards .p-card__button {
  background: #fff;
  position: absolute;
  bottom: 0;
  right: 0;
  min-width: 220px;
  height: 50px;
  line-height: 50px;
}

.p-cards .p-card__button:hover {
  background: #000;
  color: #fff;
}

.p-cards .p-card__button--label {
  text-align: center;
}

@media screen and (min-width: 767px) {
  .p-cards {
    grid-template-columns: repeat(auto-fill, minmax(50%, 1fr));
  }
}

@media (min-width: 900px) {
  .p-cards {
    grid-template-columns: repeat(auto-fill, minmax(calc(100% / 3), 1fr));
  }
}

.c-table {
  border-collapse: collapse;
  border: 1px solid #ccc;
  width: 100%;
}

.c-table th,
.c-table td {
  padding: 18px 25px;
  border-bottom: 1px solid #ddd;
}

.c-table th {
  display: block;
  background-color: #2F9839;
  color: #fff;
  text-align: center;
  width: 100%;
}

.c-table td {
  display: block;
  background-color: #fff;
  border: 1px solid #ccc;
}

@media screen and (min-width: 992px) {
  .c-table th {
    display: table-cell;
    width: 30%;
  }
  .c-table td {
    display: table-cell;
    text-align: left;
  }
}

#searchform {
  border: 1px solid #ddd;
  background: #fbfbfb;
  height: 50px;
  box-sizing: border-box;
}

#searchform #s {
  border: none;
  background: none;
  padding: 0 10px;
  margin: 0;
  width: calc(100% - 50px);
  height: 50px;
  box-sizing: border-box;
}

#searchform .submit_button {
  position: relative;
  width: 50px;
  height: 50px;
  float: right;
  top: -51px;
  top: -48px;
}

#searchform .submit_button:before {
  text-indent: 0;
  display: block;
  width: 50px;
  height: 50px;
  line-height: 50px;
  text-align: center;
  cursor: pointer;
  z-index: 1;
  position: absolute;
  font-family: 'Material Icons';
  color: #333;
  font-size: 18px;
  content: 'search';
  right: 0px;
  top: 1px;
}

#searchform #searchsubmit {
  border: none;
  background: none;
  width: 50px;
  height: 50px;
  z-index: 200;
  position: relative;
  cursor: pointer;
  display: block;
  text-indent: -300%;
  overflow: hidden;
}

