}

.verticallist .wpcf7-list-item {
  display: block;
}

#formbtn {
  display: block;
  padding: 15px;
  width: 350px;
  background: #2f9839;
  color: #fff;
  font-size: 18px;
  font-weight: 700;
  border-radius: 2px;
  margin: 25px auto 0;
}

#formbtn:hover {
  background: #fff;
  color: #ffaa56;
  border: 2px solid #ffaa56;
}

th {
  font-weight: 700;
  text-transform: uppercase;
  padding: 13px;
}

td {
  border-top: 1px solid #ededed;
  padding: 12px;
}

input,
select,
textarea {
  border: 1px solid #dfdfdf;
  letter-spacing: 1px;
  margin: 0;
  max-width: 100%;
  resize: none;
}

@media screen and (min-width: 992px) {
  .p-form01 td,
  .p-form01 th {
    display: table-cell;
  }
  .p-form01 th {
    width: 30%;
    border: solid 1px #d7d7d7;
  }
  .p-form01 td {
    width: 70%;
  }
}

.p-qa-agenda {
  margin-bottom: 30px;
  padding: 20px;
}

.p-qa-question {
  border-left: 5px solid #2f9839;
  margin-bottom: 30px;
  padding: 20px;
}

.p-qa-answer {
  border-left: 5px solid #EA5405;
  margin-bottom: 30px;
  padding: 20px;
}

.p-block {
  display: grid;
  grid-template-columns: 1fr;
  align-items: stretch;
}

.p-block__item {
  position: relative;
  height: 200px;
  margin-bottom: 50px;
  padding: 35px;
}

.p-block__item .title {
  font-size: 16px;
  font-weight: normal;
}

.p-block__button {
  background: #fff;
  position: absolute;
  bottom: 0;
  right: 0;
  min-width: 220px;
  height: 50px;
  line-height: 50px;
}

.p-block__button:hover {
  background: #000;
  color: #fff;
}

.p-block__button--label {
  text-align: center;
}

@media screen and (min-width: 767px) {
  .p-block {
    grid-template-columns: repeat(auto-fill, minmax(50%, 1fr));
  }
}

@media (min-width: 900px) {
  .p-block {
    grid-template-columns: repeat(auto-fill, minmax(calc(100% / 3), 1fr));
  }
}

.p-block01 {
